header {
  text-align: center;
  background-color: #fff;
  border-bottom: 3px solid #000;
  width: 100%;
  height: auto;
  position: relative;
}

html, body {
  flex-direction: column;
  margin: 0;
  display: flex;
  overflow-x: hidden;
}

.hamburger {
  cursor: pointer;
  font-size: 2rem;
  display: none;
  position: absolute;
  top: 10px;
  right: 15px;
}

#webtitle {
  margin: 0;
  padding-bottom: 20px;
  font-family: Jacques Francois Shadow, serif;
  font-size: 2rem;
  font-style: normal;
  display: inline-block;
}

nav a {
  color: #000;
  margin-right: 20px;
  font-size: 1rem;
  text-decoration: none;
  transition: color .3s, -webkit-text-decoration .3s, text-decoration .3s;
}

nav a:hover {
  color: #7eb7f4;
  text-decoration: none;
}

#content-layout {
  flex: 1;
  max-width: 1500px;
  margin: 0 auto;
  display: flex;
}

.us-header {
  text-align: center;
  border-bottom: 2px solid #000;
  margin-top: -10px;
  font-family: Jacques Francois Shadow;
  font-size: 2rem;
  font-weight: bold;
}

#us-section {
  background-color: #e6e6e6;
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
  border-right: 2px solid #ccc;
  flex: 1;
  padding: 20px;
}

.science-header {
  text-align: center;
  border-bottom: 2px solid #000;
  margin-top: -10px;
  font-family: Jacques Francois Shadow;
  font-size: 2rem;
  font-weight: bold;
}

#science-section {
  background-color: #e6e6e6;
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
  flex: 2;
  padding: 20px;
}

.section-title {
  font-size: 1.5rem;
  font-weight: bold;
}

img {
  max-width: 100%;
  height: auto;
}

.media-container {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.media-image {
  max-width: 60%;
  height: auto;
  margin-right: 20px;
}

.media-description {
  color: #333;
  font-size: 1rem;
  line-height: 1.5;
}

#us-section > div, #science-section > div {
  border-bottom: 2px solid #000;
  margin-bottom: 10px;
  padding-bottom: 10px;
}

#educationaltext {
  font-size: 1.3rem;
  position: absolute;
  bottom: 9px;
  left: 600px;
}

#copyright {
  padding-top: 6px;
  font-size: 1rem;
  position: absolute;
  right: 10px;
}

footer {
  position: relative;
}

@media (width <= 500px) {
  header {
    z-index: 1000;
    position: sticky;
    top: 0;
  }

  #webtitle {
    margin: 0;
    padding-bottom: 20px;
    font-family: Jacques Francois Shadow, serif;
    font-size: 1.5rem;
    font-style: normal;
    display: inline-block;
  }

  .hamburger {
    padding-top: 10px;
    font-size: 1rem;
    display: block;
  }

  nav {
    background-color: #fff;
    border: 1px solid #ccc;
    flex-direction: column;
    padding: 10px;
    display: none;
    position: absolute;
    top: 60px;
    right: 10px;
    box-shadow: 0 4px 6px #0003;
  }

  nav a {
    margin-bottom: 10px;
  }

  .nav-active {
    display: flex !important;
  }

  #content-layout {
    flex-direction: column;
    display: flex;
  }

  #us-section, #science-section {
    border: none;
    border-bottom: 2px solid #000;
    flex: 1;
    margin-bottom: 20px;
    padding: 20px;
  }

  .media-container {
    text-align: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    display: flex;
  }

  .media-image {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
  }

  .media-description {
    color: #333;
    margin-top: 10px;
    font-size: 1rem;
    line-height: 1.5;
  }

  footer {
    margin: -10px;
    display: flex;
  }

  #educationaltext {
    font-size: 8px;
    bottom: -20px;
    left: 200px;
  }

  #copyright {
    margin-top: 5px;
    padding-top: 0;
    font-size: 7px;
    bottom: -1px;
    right: 20px;
  }

  #copyrightimage {
    position: absolute;
    left: 10px;
  }
}
/*# sourceMappingURL=us&science.bde5f035.css.map */
